.main {
  background-color: #fff;
  max-width: 100%;
  display: grid;
  grid-template-rows: 50px calc(50% - 50px) 50px calc(50% - 50px);
  grid-template-columns: 20px 5px calc(100% - 50px) 5px 20px;
  grid-template-areas:
  "version version version version version"
  ". . email-container . ."
  "left-close-button . email-container . right-close-button"
  ". . email-container . .";
}

.main-mobile {
  background-color: #fff;
  display: grid;
  grid-template-rows: 50px calc(100% - 50px);
  grid-template-columns: 20px 5px calc(100% - 50px) 5px 20px;
  grid-template-areas:
  "version version version version version"
  ". email-container email-container email-container ."
  ;
}

.email-container {
  grid-area: email-container;
  background-color: #eeeeef;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 0 5px;
  padding: 0 10px;
}

.email-container .h2 {
  margin-top: 40px;
}

.email-container .h3 {
  line-height: 20px;
}

.version {
  grid-area: version;
  display: grid;
}

.left-close-button {
  grid-area: left-close-button;
  border-top: 1px solid #dcdbdc;
  border-bottom: 1px solid #dcdbdc;
  border-right: 1px solid #dcdbdc;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
}

.right-close-button {
  grid-area: right-close-button;
  border-top: 1px solid #dcdbdc;
  border-bottom: 1px solid #dcdbdc;
  border-left: 1px solid #dcdbdc;
  border-radius: 4px 0 0 4px;
  cursor: pointer;
}

.left-facing-btn {
  background-image: url(../../images/angle-left-red.svg);
}

.right-facing-btn {
  background-image: url(../../images/angle-right-red.svg);
}


.button {
  width: 100%;
  height: 100%;
}

.button:hover {
  background-color: #eeeeef;
}

.email-container li {
  margin-left: -10px;
}
